import React, { Component } from 'react'

import swal from 'sweetalert';
import Axios from '../../../lib/api';



class Setting extends Component {
  constructor(props) {
    super(props);
    this.state = {
      list: []
    }
  }
  componentDidMount = () => {

  }
  onView(id) {
    this.props.history.push(`/admin/users/${id}`);
  }
  onEdit(id) {
    this.props.history.push(`/admin/users/${id}`);
  }
  onDelete(item) {
    swal({
      title: "Are you sure?",
      text: "Once deleted, you will not be able to recover this user!",
      icon: "warning",
      buttons: true,
      dangerMode: true,
    })
      .then(willDelete => {
        if (willDelete)
          Axios.delete(`admin/users/${item.id}`).then(r => {
            console.log(r)
            var { list } = this.state;
            list = list.filter(page => page.id !== item.id);
            this.setState({ list: list })
          })
      })

  }
  render() {

    return (
      <div>Coming Soon</div>
    )
  }
}

export default Setting
